r: #FFF;\n  position: fixed;\n  z-index: 9999;\n  text-align: center;\n}\n\n#splash-screen {\n  position: fixed;\n  top: 50%;\n  left: 50%;\n  transform: translate(-50%, -50%);\n  background: white;\n}\n\n.skip {\n  position: absolute;\n  top: -70px;\n  left: 0;\n  background: #007362;\n  color: white;\n  padding: 8px;\n  z-index: 100;\n  transition: top 0.3s ease;\n}\n\n.skip:focus {\n  top: 0;\n}\n\n.app-bar {\n  padding: 8px 16px;\n  background-color: white;\n  display: flex;\n  align-items: center;\n  justify-content: space-between;\n  grid-template-columns: auto 1fr auto;\n  gap: 10px;\n  position: sticky;\n  top: 0;\n  z-index: 99;\n  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.2);\n}\n\n.app-bar .app-bar__menu {\n  display: flex;\n  align-items: center;\n}\n\n.app-bar .app-bar__menu button {\n  background-color: transparent;\n  border: none;\n  font-size: 18px;\n  padding: 8px;\n  cursor: pointer;\n}\n\n.app-bar .app-bar__brand {\n  display: flex;\n  align-items: center;\n}\n\n.app-bar .app-bar__brand img {\n  max-width: 200px;\n  user-select: none;\n}\n\n.app-bar .app-bar__navigation {\n  position: absolute;\n  top: 50px;\n  left: -180px;\n  transition: all 0.3s;\n  padding: 8px;\n  background-color: white;\n  overflow: hidden;\n}\n\n.app-bar .app-bar__navigation.open {\n  left: 0;\n}\n\n.app-bar .app-bar__navigation ul li a {\n  display: inline-block;\n  text-decoration: none;\n  color: black;\n  padding: 8px;\n  margin-bottom: 5px;\n  width: 100%;\n  border-radius: 5px;\n  transition: background-color 0.3s ease, color 0.3s ease;\n}\n\n.app-bar .app-bar__navigation ul li a:hover {\n  background-color: #007362;\n  color: #FFF;\n}\n\nmain {\n  flex: 1;\n}\n\n.section {\n  padding: 32px;\n}\n\n.content {\n  text-align: center;\n  min-height: 100%;\n}\n\n.content .content__heading {\n  font-size: 24pt;\n  font-weight: bold;\n  margin: 20px 0 0 0;\n  border-bottom: 2px solid #003f33;\n  display: inline-block;\n}\n\n.restaurants {\n  display: grid;\n  grid-template-c